import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rowparse'
})
export class RowparsePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let temp = JSON.parse(JSON.stringify(value));
    let l = [];
    temp.grid = temp.grid.split('%');
    temp.grid.forEach(i => {
      const s = i.replace(' ', '');
      if (s != '') {
        l.push(parseInt(s));
      }
    });
    const sum = l.reduce((a, b) => a + b, 0);
    if (sum % 4 > 0) { value.data.push({ grid: 25, width: 25 }); }
    return value;
  }

}
