  formatDate(index, date, tab) {
    let d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2)
      month = '0' + month;
    if (day.length < 2)
      day = '0' + day;

    let formatdate = [year, month, day].join('-');
    if (tab == "fields") {
      this.workflow[0].fields[index].value = formatdate
    }
    if (tab == "action") {
      this.workflow[0].action[this.actionIndex].depended[index].value = formatdate
    }
    if (tab == "actionchanged") {
      this.workflow[0].action[this.actionIndex].changed[index].value = formatdate
    }
    // this.selectValue(formatdate, index)
  }

  formatDateTime(index, date, tab) {
    let d = new Date(date),
      dformat = [d.getFullYear(), d.getMonth() + 1,
      d.getDate()].join('-') + ' ' +
        [d.getHours(),
        d.getMinutes(),
        d.getSeconds()].join(':');
    if (tab == "fields") {
      this.workflow[0].fields[index].value = dformat
    }
    if (tab == "action") {
      this.workflow[0].action[this.actionIndex].depended[index].value = dformat
    }
    if (tab == "actionchanged") {
      this.workflow[0].action[this.actionIndex].changed[index].value = dformat
    }
    // this.selectValue(dformat, index);
  }

