  getWorkFlowStep() {
    let body = {
      "limit": 100,
      "page": 0,
      "db_name": "trucking_db",
      "table_name": "workflow_process",
      "sort_type": "ASC",
      "sort_field": "id",
      "filter": [
        { "table_id": this.routeInfo.id }
      ]
    }
    this.CommonService.getValues(body).subscribe((response: any) => {
      if (response.statusCode == 200) {
        if (response.info.length) {
          response['info'] = response['info'].sort(function (a, b) { return a?.step - b?.step })
          this.workflowstep = response.info;
          for (let i = 0; i < this.workflowstep.length; i++) {
            this.workflowstep[i].user_id = this.workflowstep[i].user_id.split(",")
          }
          // if (this.workflowstep.length > 0) {
          // this.getWorkFlow(response.info[0].id)
          this.stepCount = this.workflowstep.length;
          // }
        }
      }

    });
  }

