  createSendpopup(templateName): void {
    this.localSend = this.dialog.open(templateName, {
      width: '800px',
      height: '900px'
    });
    this.localSend.afterClosed().subscribe((result: any) => {
      console.log(result, 'result');
      this.sendJson = {};
      if (result) {
        if (this.sendConditionArray?.length)
          this.sendConditionArray.push(result);
        else {
          this.sendConditionArray = [];
          this.sendConditionArray.push(result);
        }
      }
    })
  }

  openSendCondition(templateName, row): void {
    this.sendJson = this.sendConditionArray[row];
    const conPop = this.dialog.open(templateName, {
      width: '800px',
      height: '900px'
    });
    conPop.afterClosed().subscribe((result: any) => {
      console.log(result, 'result');
      this.sendJson = {};
      if (result) {
        if (this.sendConditionArray[row])
          this.sendConditionArray[row] = result;
      }
    })
  }

