  getDendedFields(value) {
    let body = {
      "limit": 10,
      "page": 0,
      "db_name": "trucking_db",
      "table_name": "meta_field_data",
      "sort_type": "ASC",
      "sort_field": "id",
      "search": [
        { "field_title": value }
      ],
      "filter": [
        { "table_id": this.routeInfo.id }
      ]
    }
    this.CommonService.getValues(body).subscribe((response: any) => {
      response['info'] = response['info'].filter((data: any) => data.field_sub_type !== 'duplicate');
      this.dropdownSearch = response.info;
    });
  }

