  checkDuplicateFieldAction(key, item) {
    if (this.workflow[0].action[this.actionIndex][key] && this.workflow[0].action[this.actionIndex][key].length) {
      let index: any = this.workflow[0].action[this.actionIndex][key].findIndex((data: any) => { return data.id === item.id });
      if (index === -1)
        return true;
      else
        return false;
    }
    return true;
  }

