  getUserData(value) {
    if (this.designation && this.designation.length) {
      let index: any = this.designation.findIndex((data: any) => { return data.id === value.id });
      if (index === -1) {
        this.designation.push(value);
        this.setdesignationId(value);
      }
    } else {
      this.designation = [];
      this.designation.push(value);
      this.setdesignationId(value);
    }
    console.log("desss", this.designation)
  }

  // designation users function putting as seperate function

  setdesignationId(value) {
    if (this.workflow[0].designation_id == "") {
      this.workflow[0].designation_id = [];
      this.workflow[0].designation_id.push(value.id)
    } else {
      this.workflow[0].designation_id.push(value.id)
    }
    this.userData[value.id] = []
    let body = {
      "limit": 100,
      "page": 0,
      "db_name": "trucking_db",
      "table_name": "user",
      "sort_type": "ASC",
      "sort_field": "id",
      "filter": [
        { "designation": value.id }
      ]
    }
    this.CommonService.getValues(body).subscribe((response: any) => {
      this.userData[value.id] = response.info;
    });
  }

  createWorkFlow() {
    if (this.workflow[0].hasOwnProperty("id")) {
      this.workFlowUpdate()
    } else {
      console.log("createe", this.workflow)

      this.stepCount += 1;
      let body = {
        "process_name": this.creatProcessName,
        "comments": this.createComments,
        "table_id": this.routeInfo.id,
        "table_name": this.routeInfo.table_name,
        "step": this.stepCount
      }
      this.workflow[0].table_id = this.routeInfo.id;
      this.workflow[0].table_name = this.routeInfo.table_name;
      this.workflow[0].step = this.stepCount;
      if (this.workflow[0].designation_id != "") {
        this.workflow[0].designation_id = this.workflow[0].designation_id.toString();
      }

      // if (this.workflow[0].user_id != "") {
      this.workflow[0].user_id = this.tempUser.toString();
      // }

      if (this.workflow[0].fields != "") {

        this.workflow[0].fields = JSON.stringify(this.workflow[0].fields);
      } else {
        this.workflow[0].fields = "";
        console.log("wwww", this.workflow[0])

      }
      // if (this.sendConditionArray?.length)
      //   this.workflow[0].action['Send'] = this.sendConditionArray;

      if (this.workflow[0].action != "") {
        this.workflow[0].action = JSON.stringify(this.workflow[0].action);
      }

      if (this.workflow[0].decision != "") {
        this.workflow[0].decision = JSON.stringify(this.workflow[0].decision);
      }

      this.CommonService.createWorkFlow(this.workflow[0], "workflow_process").subscribe((response: any) => {
        if (response.statusCode == 200) {
          this.creatProcessName = ""
          this.createComments = ""
          // this.getWorkFlowStep();
          // this.closeDialog();
        }
        this.getWorkFlowStep()
      });

    }

  }

  workFlowUpdate() {

    if (this.workflow[0].designation_id != "") {
      this.workflow[0].designation_id = this.workflow[0].designation_id.toString();
    }

    // if (this.workflow[0].user_id != "") {
    this.workflow[0].user_id = this.tempUser.toString();
    // }

    if (this.workflow[0].fields != "") {

      this.workflow[0].fields = JSON.stringify(this.workflow[0].fields);
    } else {
      this.workflow[0].fields = "";
    }
    console.log("wwww", this.workflow[0])
    // if (this.sendConditionArray?.length)
    //   this.workflow[0].action['Send'] = this.sendConditionArray;

    if (this.workflow[0].action != "") {
      this.workflow[0].action = JSON.stringify(this.workflow[0].action);
    }

    if (this.workflow[0].decision != "") {
      this.workflow[0].decision = JSON.stringify(this.workflow[0].decision);
    }

    this.CommonService.workFlowUpdates(this.workflow[0]).subscribe((response: any) => {
      // this.getWorkFlow(this.processId)
      this.getWorkFlowStep()
    });

  }

