  goToProcess() {
    if (this.processData.process == "view-all") {
      this.router.navigate(['/view-all-process']);
    }
    else {
      let temp = { id: this.processData.id, tablename: this.processData.table_name, }
      this.router.navigate(['/dashboard/view', this.processData.table_name], { queryParams: temp, });
    }
  }

  getGroupField() {
    if (this.selectedGroup?.id) {
      const filter: any = {
        group_id: this.selectedGroup?.id
      };
      this.metaDataGroup = {};
      this.fieldLoader = true;
      this.processDashboard.getGroupFields(this.processData.table_name, filter).subscribe((response: any) => {
        this.fieldLoader = false;
        if (response.statusCode === 200) {
          const temp: any = response.info;
          if (temp && temp[0] && temp[0].data) {
            temp.sort(function (a, b) { return a?.row_no - b?.row_no });
            temp.forEach(element => {
              element.grid = '';
              if (element?.data?.length) {
                element.data.sort(function (a, b) { return a?.field_position - b?.field_position });
                element.data.forEach(rowelement => {
                  if (rowelement.field_type === 'table') {
                    rowelement.grid = '100%'
                  }
                  if (rowelement?.grid) {
                    rowelement.width = parseInt(rowelement.grid);
                    if (element.grid.length) {
                      element.grid += ' ' + (rowelement.grid + '%');
                    } else {
                      element.grid = rowelement.grid + '%';
                    }
                  }
                  rowelement.position = rowelement.field_position;
                });
              }
            });
          }
          this.metaDataGroup = {};
          this.metaDataGroup[this.selectedGroup?.id] = temp;
        }
      });
      this.getAllsubTbles();
    }
  }

  getAllsubTbles() {
    const data: any = {
      'group_id': this.selectedGroup?.id,
    };
    this.processDashboard.getAllSubTables(data).subscribe((response: any) => {
      if (response?.statusCode == 200) {
        this.metaData = response['info'] ? response['info'] : [];
        if (this.metaData?.length) {
          this.metaData.forEach((value: any) => {
            if (value?.data) {
              value.data.sort(function (a, b) { return a?.field_position - b?.field_position });
            }
            if (value?.parent_table_id) {
              const index = this.metaData.findIndex((a: any) => { return a.id === parseInt(value.parent_table_id) });
              if (index > -1) {
                value.parent_display_name = this.metaData[index].display_name;
              }
            }
          });
        }
      }
    });
  }
  getFormGroup() {
    this.formGroups = [];
    this.processDashboard.getFormsGroup('', this.processData?.id).subscribe((response: any) => {
      if (response.statusCode === 200) {
        this.formGroups = response['info'];
        if (this.formGroups && this.formGroups.length)
          this.selectedGroup = this.formGroups[0];
        this.currentGroupName = this.selectedGroup?.group_name;
        this.getGroupField();
      }
    });
  }

