  getFlowData() {
    const filter: any = {
      table_name: 'workflow_process',
      table_field: 'table_name',
      value: this.processData?.table_name,
      db: 'tracking_db'
    };
    this.processDashboard.getWorkflowList(filter).subscribe((response: any) => {
      if (response && response['info']) {
        if (response['info'].length) {
          response['info'].forEach((element: any) => {
            if (element.fields)
              element.fields = JSON.parse(element.fields);
          });
        }
        this.stepGroup = response['info'];
        if (this.stepGroup?.length)
          this.selectedStep = this.stepGroup[0];
        else
          this.selectedStep = {};
        console.log(this.stepGroup, 'step group');

      }
    });
  }

