import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Inject, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { noop } from 'rxjs';
import { groupBy } from 'rxjs/internal/operators/groupBy';
import { CommonService } from 'src/app/common.service';
import { field } from '../constant';
import { ProcessDashboardService } from '../process-dashboard/process-dashboard.service';
let addFlowPopup: MatDialogRef<any>;

@Component({
  selector: 'app-process-workflow',
  templateUrl: './process-workflow.component.html',
  styleUrls: ['./process-workflow.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProcessWorkflowComponent implements OnInit {
  @Input() process: any;
  @Input() tab: any;
  @Input() currentStep: any;
  @Output() resultProcess = new EventEmitter<any>();
  flowData: any = [];
  viewConfirmation: any = false;
  deleteId: any = '';
  openedProcess: any = {};
  buttonList: any = [
    { checked: false, display: 'Save', name : 'Save', field: [], designation: [],user: [] },
    { checked: false, display: 'Cancel', name : 'Cancel', field: [], designation: [],user: [] },
    { checked: false, display: 'Submit', name : 'Submit', field: [], designation: [],user: [] },
    { checked: false, display: 'Reassign', name : 'Reassign', field: [], designation: [],user: [] },
    { checked: false, display: 'Assign to', name : 'Assign to', field: [], designation: [],user: [] },
    { checked: false, display: 'Send Back', name : 'Send Back', field: [], designation: [],user: [] }
  ];
  actionList: any = [];
  selectedType: any = 'assigne';
  assigneeList: any;
  flowJson: any = {};
  selectedDesig: any;
  userList: any;
  currentTableFieldList: any;
  fieldType: any = ['text','telephone', 'email','date', 'checkbox','radio','select','attachment', 'slider','checklist','switch', 'datetime-local','currency', 'time', 'textarea', 'number', 'image', 'link', 'richtext', 'sequence', 'duplicate'];
  selectedField: any = [];
  typePng: any =
    {'approval': {url: '../assets/workflow-icons/Document Upload.png', display_name: 'Approval'} , 'send_mail': {url: '../assets/workflow-icons/Mail Receive.svg', display_name: 'Send Mail'}, 'date_receive': {url: '../assets/workflow-icons/Date Receive.png',display_name: 'Date Receive' }, 'date_upload': {url: '../assets/workflow-icons/Date Upload.png', display_name: 'Date Upload'}, 'document_receive': {url: '../assets/workflow-icons/Document Receive.png',display_name: 'Document Receive' }, 'document_upload': {url: '../assets/workflow-icons/Document Upload.png', display_name: 'Document Upload'}}
  
  constructor(private flowdialog: MatDialog, public processDashboard: ProcessDashboardService,  public commonService: CommonService, public cd: ChangeDetectorRef) { }

  ngOnInit(): void {
    console.log(this.process, 'process');

    if (this.process && this.process.table_name && this.tab === 'workflow') {
      this.getFlowData();
      this.getUserList();
    } else if (this.process && this.process.table_name && this.tab === 'action') {
      console.log(this.currentStep, 'current step');
      this.setAction();
    }
  }

  onUpdateUserList() {
    if ( this.selectedDesig?.length && this.userList) {
      const userId: any = this.flowJson.user_id.split(',');
      this.selectedDesig.forEach((element: any) => {
        element.user_id = [];
        if (this.userList[element.id]) {
          this.userList[element.id].forEach((data: any) => {
            const index = userId.findIndex((a: any) => { return a == data.id })
            if (index >= 0) {
              element.user_id.push(data.id);
            }
          });
        }
      });
    }
  }

  getUserList() {
    this.processDashboard.getUserList().subscribe((response: any) => {
      if (response?.statusCode === 200) {
        this.userList = response['info'].reduce((acc, curr) => {
          if (!acc[curr.designation]) acc[curr.designation] = []; //If this type wasn't previously stored
          acc[curr.designation].push(curr);
          return acc;
        }, {});;
        if(this.flowData[0]?.user_id?.length) {
          this.onUpdateUserList();
        }
      }
    });
  }

  setAssignee() {
    this.selectedDesig = [];
    console.log(this.selectedDesig, 'selected desig');
    if (this.flowJson && this.flowJson?.designation_id && this.flowJson?.designation_id.length) {
      this.assigneeList.forEach((element: any) => {
        if (this.flowJson?.designation_id === element.id) {
            this.selectedDesig.push(element);
        }
      });
      console.log(this.selectedDesig, 'selected desig');

      this.selectedUser();
    }
  }

  fieldTypeSelect(type) {
    this.processDashboard.getFieldByType(type, this.process.id).subscribe((response: any) => {
      if (response?.statusCode === 200) {
        this.currentTableFieldList = response['info'];
      }
    });
  }

  createAssigne() {
    if (this.flowJson?.designation_id && this.flowJson?.user_id) {
      console.log(this.selectedDesig, 'selected desig');
      this.setAssignee();
    }
  }
  selectedUser() {
    if (this.selectedDesig?.length) {
      this.flowJson.user_id = '';
      this.selectedDesig.forEach((element: any, index: number) => {
        if (element?.user_id?.length) {
          const data = element.user_id.join();
          this.flowJson.user_id = this.flowJson?.user_id?.length ? this.flowJson?.user_id +  ',' + data : data;
        }
      });
    } else {
      this.flowJson.user_id = '';
    }
  }

  deleteDesig(data, row) {
    this.selectedDesig.splice(row, 1);
    if (this.selectedDesig.length) {
      this.flowJson.designation_id = this.selectedDesig.map(a => a.id);
    } else {
      this.flowJson.designation_id = '';
    }
    this.selectedUser();
  }

  setAction() {
    if (this.currentStep?.action && this.currentStep?.action.length) {
      const action =  JSON.parse(this.currentStep.action);
      this.actionList = action.length ? action : [];
      if (this.actionList.length) {
        this.buttonList.forEach((element: any) => {
          element.checked = false;
        });
        this.actionList.forEach((element) => {
          const index: any = this.buttonList.findIndex((data: any) => { return data.name === element.name });
          if (index > -1) {
            this.buttonList[index].checked = true;
          }
        });
      }
    } else {
      this.actionList = [];
      this.buttonList.forEach((element: any) => {
        element.checked = false;
      });
    }
  }

  saveAction() {
    if (this.actionList && this.actionList.length) {
      const payload: any = {
        id: this.currentStep.id,
        action: JSON.stringify(this.actionList)
      }
      this.processDashboard.updateWorkflowProcess(payload).subscribe((response: any) => {
        if (response?.statusCode === 200) {
        }
      });
    }
  }

  getFlowData() {
    const filter: any = {
      table_name: 'workflow_process',
      table_field: 'table_name',
      value: this.process?.table_name,
      db: 'tracking_db'
    };
    this.processDashboard.getOptionsList(filter).subscribe((response: any) => {
      if (response && response['info']) {
        if (response['info']?.length) {
          response['info'].forEach((element: any) => {
            if (element.fields)
              element.fields = JSON.parse(element.fields);
            if (element.decision)
              element.decision = JSON.parse(element.decision);
            if (element.action)
              element.action = JSON.parse(element.action);
            
            if (element?.user_id?.length) {
              element.userIds = element.user_id.split(',');
            }
          });
        };
        response['info'].sort(function (a, b) { return a?.step - b?.step });
        this.flowData = response['info'];
        this.cd.detectChanges();
        console.log(this.flowData, 'flow data');

      }
    });
  }
  stepDragEnd(event: CdkDragDrop<string[]>) {
    console.log('am ');
    
    moveItemInArray(this.flowData, event.previousIndex, event.currentIndex);
    const updatePosition: any = [];
    this.flowData.forEach((element: any, index) => {
      updatePosition.push({ id: element.id, step: index });
    });
    this.processDashboard.putColumns(updatePosition, 'workflow_process').subscribe((response: any) => {
      if (response.statusCode === 200) { }
    });
  }

  openWorkFlow(step) {
      let data: any = {};
      data.step = step;
      this.openPopup(data);
  }
  editSteps(data) {
    this.openPopup(data);
  }

  openWorkFlowUnderProcess(data, index) {
    this.openedProcess = data;
    this.openPopup({});
  }

  editProcessWorkFlow(data) {
    data.name = 'Process';
    this.openPopup(data);
  }

  editFieldWorkFlow(field, index, data) {
    field.name = 'Field';
    data.field_index = index;
    this.openedProcess = data;
    this.openPopup(field);
  }

  openPopup(data) {
    const flowItem = data;
    addFlowPopup = this.flowdialog.open(FlowDialogComponent, {
      width: '648px',
      height: '700px',
      disableClose: true,
      data: {processData: this.process, flowData: flowItem}
    });
    addFlowPopup.afterClosed().subscribe((result: any) => {
      if (result && result?.type === 'Process') {
        this.openedProcess = {};
      } else if(result && result?.type === 'Decision' && result?.flow){
        console.log(result, 'result');
        if (result.flow?.selectedField) {
          result.flow.display_name = result.flow?.selectedField?.field_title;
          result.flow.name = result.flow?.selectedField?.name;
          result.flow.id = result.flow?.selectedField?.id;
          result.flow.type = result.flow?.selectedField?.field_sub_type;
        }
        delete result.flow?.selectedField;
        let decision: any = this.openedProcess.decision && this.openedProcess.decision?.length ? this.openedProcess.decision : [];

        if (decision.length && this.openedProcess?.field_index) {
          decision[this.openedProcess.field_index] = result.flow;
        } else if(decision.length) {
          decision.push(result.flow);
        } else {
          decision = [];
          decision.push(result.flow);
        }
        delete this.openedProcess.name;
        this.openedProcess.decision = JSON.stringify(decision);
        delete this.openedProcess.fields;
        delete this.openedProcess.action;
        delete this.openedProcess.field_index;
        console.log(this.openedProcess, 'opened process');
        this.processDashboard.updateWorkflowProcess(this.openedProcess).subscribe((response: any) => {
          if (response?.statusCode === 200 && this.openedProcess.id) {
            this.getFlowDatabyId(this.openedProcess.id);
          }
        });
        let fields: any = this.openedProcess.decision && this.openedProcess.decision?.length ? this.openedProcess.decision : [];
      } else if(result && result?.flow) {
        result.flow.designation_id = result.flow?.designation_id?.length ? result.flow.designation_id.join() : '';
        if (result.flow?.id) {
          this.processDashboard.updateWorkflowProcess(result.flow).subscribe((response: any) => {
            if (response?.statusCode === 200) {
              this.getFlowData();
            }
          });
        } else {
          result.flow.table_id = this.process.id;
          result.flow.table_name = this.process.table_name;
          this.processDashboard.insertWorkflowProcess(result.flow).subscribe((response: any) => {
            if (response?.statusCode === 200) {
              this.getFlowData();
            }
          });
        }
      }
    });
  }
  getFlowDatabyId(id) {
    if (id) {
      const filter: any = {
        table_name: 'workflow_process',
        table_field: 'table_name',
        value: this.process?.table_name,
        db: 'tracking_db',
        id: id
      };
      this.processDashboard.getOptionsList(filter).subscribe((response: any) => {
        if (response && response['info']) {
          if (response['info'].length) {
            response['info'].forEach((element: any) => {
              if (element.fields)
                element.fields = JSON.parse(element.fields);
              if (element.decision)
                element.decision = JSON.parse(element.decision)
            });
            this.currentStep = response['info'][0];
            if (this.currentStep?.fields)
            this.currentStep.fields = JSON.parse(JSON.stringify(this.currentStep.fields));
            if (typeof this.currentStep?.decision === 'string' && this.currentStep?.decision.length)
              this.currentStep.decision =  JSON.parse(this.currentStep.decision);
            this.flowData = [];
            this.flowData.push(this.currentStep);
            this.cd.detectChanges();
            console.log(this.currentStep, 'curren step');
          }
        }
      });
    }
  }
  emitConfirmation(data) {
    this.viewConfirmation = false;
    data ? this.deleteWorkflow() : noop()
  }
  deleteWorkflow() {
    if (this.deleteId) {
      const payload: any = {
        id: this.deleteId,
        tablename: 'workflow_process'
      }
      this.commonService.commonDelete(payload).subscribe((response: any) => {
        if (response?.statusCode === 200) {
          this.resultProcess.emit();
        }
      });
    }
  }
  deleteFields(row,column) {
    if(this.flowData[row]?.fields[column])
    this.flowData[row].fields.splice(column, 1);
    if (this.flowData[row]) {
      const payload: any = JSON.parse(JSON.stringify(this.flowData[row]));
      if(payload.fields)
        payload.fields = JSON.stringify(payload.fields);
      if(payload.decision)
        payload.decision = JSON.stringify(payload.decision);
      if(payload.action)
        payload.action = JSON.stringify(payload.action);
      console.log(payload, 'payload');
      this.processDashboard.updateWorkflowProcess(payload).subscribe((response: any) => {
        if (response?.statusCode === 200) {
        }
      });
    }

  }
  checkForActionList() {
    this.actionList = [];
    this.buttonList.forEach((element: any) => {
      if (element.checked) {
        this.actionList.push(element);
      }
    });
    this.saveAction();
  }
  openField(data, row) {
    const payload: any = {
      type: data,
      value: []
    };

    if (data === 'depended' ) {
      payload.value = this.actionList[row]?.field && this.actionList[row].field.length ? this.actionList[row].field : [] ;
    } else if (data === 'designation') {
      payload.value = this.actionList[row]?.designation && this.actionList[row].designation.length ? this.actionList[row].designation : [];
    } else if (data === 'users') {
      payload.value = this.actionList[row]?.user && this.actionList[row].user.length ? this.actionList[row].user : [];
    }
    addFlowPopup = this.flowdialog.open(FieldSelectComponent, {
      width: '648px',
      height: '600px',
      disableClose: true,
      data: {processData: this.process, fields: payload}
    });
    addFlowPopup.afterClosed().subscribe((result: any) => {
      console.log(result, 'result');
      if (result && result.type === 'depended' ) {
        this.actionList[row].field = result.value;
        this.saveAction();
      } else if (result && result.type === 'designation') {
        this.actionList[row].designation = result.value;
        this.saveAction();
      } else if (result && result.type === 'users') {
        this.actionList[row].user = result.value;
        this.saveAction();
      }
      this.cd.detectChanges();
    });

  }
}

@Component({
  selector: 'app-add-flow',
  templateUrl: 'add-flow.component.html',
  styleUrls: ['./process-workflow.component.css']
})
export class FlowDialogComponent implements OnInit {
  workflowIcon: any =[
    { url: '../assets/workflow-icons/Process.svg', name: 'Process'},
    { url: '../assets/workflow-icons/Field.svg', name: 'Field'},
    { url: '../assets/workflow-icons/Decision.svg', name: 'Decision'},
    { url: '../assets/workflow-icons/Document.svg', name: 'Document'},
    { url: '../assets/workflow-icons/Date.svg', name: 'Date' },
    { url: '../assets/workflow-icons/Flow.svg', name:'Flow'},
    { url: '../assets/workflow-icons/Terminate.svg', name: 'Terminate'},
    { url: '../assets/workflow-icons/Notify.svg', name: 'Save'},
  ];
  selectedWork: any = {};
  step: any = 0;
  flowtab: any = 'settings';
  cancelBack: any = 'Cancel';
  nextDone: any = 'Next';
  disableBtn: any = false;
  assigneeList: any = [];
  selectedDesig: any = [];
  currentTableFieldList: any;
  userList: any = [];
  flowJson: any = {};
  userIdList: any = [];
  fieldType: any = ['text','telephone', 'email','date', 'checkbox','radio','select','attachment', 'slider','checklist','switch', 'datetime-local','currency', 'time', 'textarea', 'number', 'image', 'link', 'richtext', 'sequence', 'duplicate'];
  processList: any = [];
  selectedField: any = {};
  flowType: any = [
    {display_name: 'Approval', name: 'approval' }, { display_name: 'Send Mail', name: 'send_mail' }, {display_name: 'Date Receive', name: 'date_receive'}, {display_name: 'Date Upload', name: 'date_upload'}, {display_name: 'Document Receive', name: 'document_receive'}, {display_name: 'Document Upload', name: 'document_upload'}
  ];
  currentTableFieldListCopy: any = [];
  actionList: any = [
    {name: 'send', display: 'Send'}, {name: 'approve', display: 'Approve'}, {name: 'send_back', display: 'Send Back'}, {name: 'send_approval', display: 'Send Approval'}
  ];
  currentAction: any = {};
  designAction: any = false;
  dependAction: any = false;
  changedAction: any = false;
  fieldSelected: any = [];
  actionJson: any = {send: {},approve: {},send_back: {},send_approval: {}};

  constructor(@Inject(MAT_DIALOG_DATA) public data: any, public flowd: MatDialogRef<FlowDialogComponent>,public processDashboard: ProcessDashboardService,  public cd: ChangeDetectorRef) {}
  ngOnInit(): void {
    console.log(this.data, 'data');
    if (this.data?.flowData?.id) {
      this.flowJson = this.data.flowData;
      if (this.data?.flowData?.fields?.length) {
        this.fieldSelected = this.data?.flowData?.fields;
      }
      if (this.flowJson?.action) {
        this.actionJson = this.flowJson.action;
        console.log(this.actionJson, 'actionjson if');
      } else {
        console.log(this.actionJson, 'actionjson else');
      }
    }
    this.processDashboard.getDesignationList().subscribe((response: any) => {
      if (response?.statusCode === 200 && response?.info.length) {
        this.assigneeList = response['info'];
        this.assigneeList.forEach(element => {
            element.id = element.id.toString();
        });
        if (this.data?.flowData.designation_id?.length) {
          if(this.data?.flowData?.designation_id?.length) {
            console.log(this.data?.flowData?.designation_id, 'design');
            if (typeof this.data?.flowData?.designation_id === 'string')
              this.flowJson.designation_id = this.data?.flowData?.designation_id.split(',').map(String);
          }
          this.selectedAssignee('');
          if(this.data?.flowData.user_id?.length) {
            this.flowJson.user_id =  this.data?.flowData.user_id.toString();
            this.onUpdateUserList();
          }
        }
      }
    });
    this.getAdvancedApi();
    this.getUserList();
    this.flowJson.step = this.data?.flowData?.step;
    console.log(this.flowJson, 'flowjson');

  }

  selectedFlowType(type) {
    this.selectedWork = type;
    this.disableBtn = false;
    this.flowJson.type = type.name.toLowerCase();
    if (this.selectedWork.name === 'Process') {
      this.flowJson = {};
      if (this.data?.flowData?.id)
        this.flowJson.id = this.data.flowData.id;

      this.flowJson.process_name = this.data.flowData?.process_name ? this.data.flowData.process_name : '';
      this.flowJson.table_id = this.data?.processData.id;
      this.flowJson.table_name = this.data?.processData.table_name;
      this.flowJson.step = this.data?.flowData?.step;
      this.flowJson.designation_id = '';
      this.flowJson.user_id = '';
      this.flowJson.comments = this.data?.flowData?.comments ? this.data.flowData.comments : '';
      if(this.data?.flowData.designation_id?.length) {
        this.flowJson.designation_id = this.data.flowData.designation_id.split(',').map(String);
      }
    } else if (this.selectedWork.name === 'Field') {
      this.flowJson = {};
      this.flowJson.isCheck = (this.data.flowData?.isCheck || this.data.flowData?.isCheck == 0) ? this.data.flowData.isCheck : 0;
      this.flowJson.field_type = this.data.flowData?.field_type ? this.data.flowData.field_type : '';
      this.flowJson.check_value = this.data.flowData?.check_value ? this.data.flowData.check_value : '';
      const filter: any = {
        db_name: 'tracking_db',
        name: 'meta_table_data'
      };
      this.processDashboard.getCommonValues(filter).subscribe((response: any) => {
        if (response?.statusCode === 200) {
          this.processList = response['info'];
        }
      });
    } else if (this.selectedWork.name === 'Document') {
      this.flowJson = {};
    } else if (this.selectedWork.name === 'Date') {
      this.flowJson = {};
      this.flowJson.isCheck = (this.data.flowData?.isCheck || this.data.flowData?.isCheck == 0) ? this.data.flowData.isCheck : 0;
      this.flowJson.date = this.data.flowData?.date ? this.data.flowData.date : '';
      this.flowJson.isFutureDate = (this.data.flowData?.isFutureDate || this.data.flowData?.isFutureDate == 0) ? this.data.flowData.isFutureDate : 0;
    } else if (this.selectedWork.name === 'Decision') {
      this.flowJson = {}
      this.flowJson.isCheck = (this.data.flowData?.isCheck || this.data.flowData?.isCheck == 0) ? this.data.flowData.isCheck : 0;
      this.flowJson.field_type = this.data.flowData?.field_type ? this.data.flowData.field_type : '';
      this.flowJson.yes_check_value = this.data.flowData?.yes_check_value ? this.data.flowData.yes_check_value : '';
      this.flowJson.no_check_value = this.data.flowData?.no_check_value ? this.data.flowData.no_check_value : '';
    }

  }
  onConfirm() {
    if (this.fieldSelected && this.fieldSelected.length) {
      this.flowJson.fields = JSON.stringify(this.fieldSelected);
    }
    if (this.actionJson?.send?.selectedDesig) {
      delete this.actionJson.send.selectedDesig;
    }
    this.actionList.forEach((element: any) => {
      if (this.actionJson[element.name]?.selectedDesig?.length) {
        this.actionJson[element.name].user_id = '';
        this.actionJson[element.name].selectedDesig.forEach((value: any, index: number) => {
          if (value?.user_id?.length) {
            const data = value.user_id.join();
            this.actionJson[element.name].user_id = this.actionJson[element.name]?.user_id.length ? this.actionJson[element.name].user_id +  ',' + data : data;
          }
        });
      } else {
        this.actionJson[element.name].user_id = '';
      }
    });
    this.flowJson.action = JSON.stringify(this.actionJson);
      console.log(this.flowJson, 'flowjson');
      console.log(this.actionJson, 'flow json');
      addFlowPopup.close({ flow: this.flowJson, type: this.selectedWork.name });
  }
  onBack() {
    addFlowPopup.close(false);
  }
  selectedAssignee(asignee) {
    this.setAssignee();
  }

  setAssignee() {
    this.selectedDesig = [];
    if (this.flowJson && this.flowJson?.designation_id && this.flowJson?.designation_id.length) {
      this.assigneeList.forEach((element: any) => {
        let index = -1;
        index = this.flowJson?.designation_id?.findIndex((a: any) =>{ return a.toString() === element.id.toString() });
        if (index > -1) {
            this.selectedDesig.push(element);
        }
      });
    }
  }

  onUpdateUserList() {
    if ( this.selectedDesig.length && this.userList) {
      const userId: any = this.flowJson.user_id.split(',');
      this.selectedDesig.forEach((element: any) => {
        element.user_id = [];
        if (this.userList[element.id]) {
          this.userList[element.id].forEach((data: any) => {
            const index = userId.findIndex((a: any) => { return a == data.id })
            if (index >= 0) {
              element.user_id.push(data.id);
            }
          });
        }
      });
    }
  }
  getAdvancedApi() {
    const filter: any = {
      name: 'meta_field_data',
      table_name: this.data?.processData?.table_name
    }
    this.processDashboard.getCommonValues(filter).subscribe((response: any) => {
      if (response?.statusCode === 200)
        response['info'] = response['info'].filter((data: any) => data.field_sub_type !== 'duplicate');
        this.currentTableFieldList = response['info'];
        this.currentTableFieldListCopy = response['info'];
      // if (this.currentTableFieldList?.length && this.data.flowData?.name !== 'Process' && this.data.flowData?.id) {
      //   this.currentTableFieldList.forEach(element => {
      //     if (element.id === this.data.flowData.id)
      //       this.flowJson.selectedField = element;
      //   });
      // }

    });
  }

  searchDuplicate(val) {
    const data: any = JSON.parse(JSON.stringify(this.currentTableFieldListCopy))
    this.currentTableFieldList = data.filter(i => i['field_title'].toLowerCase().includes(val.toLowerCase()))
  }

  getUserList() {
    this.processDashboard.getUserList().subscribe((response: any) => {
      if (response?.statusCode === 200) {
        this.userList = response['info'].reduce((acc, curr) => {
          if (!acc[curr.designation]) acc[curr.designation] = []; //If this type wasn't previously stored
          acc[curr.designation].push(curr);
          return acc;
        }, {});;
        if(this.data?.flowData.user_id?.length) {
          this.onUpdateUserList();
        }
      }
    });
  }

  selectedUser() {
    if (this.selectedDesig?.length) {
      this.flowJson.user_id = '';
      this.selectedDesig.forEach((element: any, index: number) => {
        if (element?.user_id?.length) {
          const data = element.user_id.join();
          this.flowJson.user_id = this.flowJson?.user_id?.length ? this.flowJson?.user_id +  ',' + data : data;
        }
      });
    } else {
      this.flowJson.user_id = '';
    }
  }

  deleteDesig(data, row) {
    this.selectedDesig.splice(row, 1);
    if (this.selectedDesig.length) {
      this.flowJson.designation_id = this.selectedDesig.map(a => a.id);
    } else {
      this.flowJson.designation_id = '';
    }
    this.selectedUser();
  }

  deleteField(row) {
    this.fieldSelected.splice(row, 1)
  }
  deleteDependaction(action, row) {}
  deleteChangedaction(action, row) {}

  selectedType(type) {
    this.processDashboard.getFieldByType(type, this.data.processData.id).subscribe((response: any) => {
      if (response?.statusCode === 200) {
        this.currentTableFieldList = response['info'];
      }
    });
  }

  selectField(field) {
    let value: any = {};
    value.display_name = field.field_title;
    value.id = field.id;
    value.name = field.name;
    value.type = field.field_sub_type;
    if (this.fieldSelected && this.fieldSelected?.length) {
      let index: any = this.fieldSelected.findIndex((data: any) => { return data.id === field.id });
      if (index === -1) {
        this.fieldSelected.push(value);
      }
    } else {
      this.fieldSelected.push(value);
    }
  }

  selectactionField(field, key) {
    let value: any = {};
    value.display_name = field.field_title;
    value.id = field.id;
    value.name = field.name;
    value.type = field.field_sub_type;
    if (this.actionJson[this.currentAction.name] && this.actionJson[this.currentAction.name][key]?.length) {
      let index: any = this.actionJson[this.currentAction.name][key].findIndex((data: any) => { return data.id === field.id });
      if (index === -1) {
        this.actionJson[this.currentAction.name].key.push(value);
      }
    } else {
      this.actionJson[this.currentAction.name][key] = [];
      this.actionJson[this.currentAction.name][key].push(value);
    }
  }
  selectactionchanged(field) {

  }

  selectedUserForaction(user) {}

  actionTab() {
    if (this.selectedDesig?.length) {
      const val: any = JSON.parse(JSON.stringify(this.selectedDesig));
      val.forEach(element => {
        element.user_id = [];
      });
      this.actionJson.approve.selectedDesig = JSON.parse(JSON.stringify(val));
      this.actionJson.send_back.selectedDesig = JSON.parse(JSON.stringify(val));
      this.actionJson.send_approval.selectedDesig = JSON.parse(JSON.stringify(val));
      console.log(this.actionJson, 'val');

      if ( this.selectedDesig.length && this.userList) {
        this.actionList.forEach((element: any) => {
          if ( this.actionJson[element.name]?.user_id?.length) {
            const userId: any = this.actionJson[element.name].user_id.split(',');
            this.actionJson[element.name].selectedDesig.forEach((value: any) => {
              value.user_id = [];
              if (this.userList[value.id]) {
                this.userList[value.id].forEach((data: any) => {
                  const index = userId.findIndex((a: any) => { return a == data.id })
                  if (index >= 0) {
                    value.user_id.push(data.id);
                  }
                });
              }
            });
          }
        });
      }
    }
  }

}

@Component({
  selector: 'app-field-select',
  templateUrl: 'field-select.component.html',
  styleUrls: ['./process-workflow.component.css']
})
export class FieldSelectComponent implements OnInit {
  assigneeList: any = [];
  currentTableFieldList: any = [];
  type: any = '';
  userList: any = [];
  result: any = [];
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,public processDashboard: ProcessDashboardService) {}
  ngOnInit() {
    this.type = this.data?.fields?.type;
    this.result = this.data.fields?.value ? this.data.fields?.value : [];
    if (this.data?.fields?.type === 'designation') {
      this.processDashboard.getDesignationList().subscribe((response: any) => {
        if (response?.statusCode === 200 && response?.info.length) {
          this.assigneeList = response['info'];
        }
      });
    } else if(this.data?.fields?.type === 'depended') {
      this.getAdvancedApi();
    } else if(this.data?.fields?.type === 'users') {
      this.getUserList();
    }
  }

  onConfirm() {
    if (this.result?.length) {
      if (this.data?.fields?.type === 'designation') {
        addFlowPopup.close({type: this.type, value: this.result});
      } else if(this.data?.fields?.type === 'depended') {
        addFlowPopup.close({type: this.type, value: this.result });
      } else if(this.data?.fields?.type === 'users') {
        addFlowPopup.close({type: this.type, value: this.result });
      }
    } else {
      addFlowPopup.close(false);
    }
  }

  getAdvancedApi() {
    const filter: any = {
      name: 'meta_field_data',
      table_name: this.data?.processData?.table_name
    }
    this.processDashboard.getCommonValues(filter).subscribe((response: any) => {
      if (response?.statusCode === 200)
        this.currentTableFieldList = response['info'];
    });
  }

  getUserList() {
    this.processDashboard.getUserList().subscribe((response: any) => {
      if (response?.statusCode === 200) {
        this.userList = response['info'];
      }
    });
  }

}
